import { useState } from 'react';
import { AiFillFilter } from 'react-icons/ai'
import { FaTimes } from 'react-icons/fa';
import FilterType from './FilterType';
import ButtonAdd from './ButtonAdd';

const SideBar = ({data, ...props}) => {
    const [isExpanded, setIsExpanded] = useState(false);

    function addLabel(name) {
        var newLabel = {name, id: Math.ceil(Math.random() * 10000)};

        props.setData({...data, labels: [...data.labels, newLabel]});

        fetch(`https://my-json-server.typicode.com/LikeAMantis/react-task-tracker/labels`, {
          method: 'POST',
          headers: {
              'Content-Type': 'application/json'
            },
          body: JSON.stringify(newLabel),
        });
    }

    function deleteLabel(id) {
        fetch(`https://my-json-server.typicode.com/LikeAMantis/react-task-tracker/labels/${id}`, {method: 'DELETE'});
        props.setData({...data, labels: data.labels.filter(label => label.id !== id)});
    }

    return (
        <div className="sideBar" expanded={isExpanded ? "true" : "false"}>
            <div className="sideBar-header" onClick={() => setIsExpanded(!isExpanded)}>
                <AiFillFilter />
                <span>Filter</span>
            </div>
            <div className="filterOption-wrapper" active={props.filterState.type === "all" ? "true" : "false"}>
                <div className="filterOption" style={{marginBottom: "15px"}} onClick={() => props.setFilterState({type: "all"})}>All</div>
            </div>
            <FilterType {...props} type="color">
                {data.colors && data.colors.map((color) => (
                    <div className="filterOption-wrapper" key={`colorFilter-${color.id}`} active={props.filterState.type === "color" &&  props.filterState.value === color.id ? "true" : "false"}>
                        <div 
                            className="filterOption" 
                            style={{background: `var(--${color.name})`, color: "black"}}  
                            data-id={color.id}
                        >
                            {color.name}
                        </div>
                    </div>
                ))}
            </FilterType>
            <FilterType {...props} type="label">
                {data.labels && data.labels.map(label => (
                    <div className="filterOption-wrapper" key={`labelFilter-${label.id}`} active={(props.filterState.type === "label" &&  props.filterState.value === label.id) ? "true" : "false"}>
                        <div 
                            className="filterOption"  
                            data-id={label.id} 
                        >
                            {label.name}
                            <FaTimes className="del-icon" onClick={(e) => deleteLabel(parseInt(e.target.closest(".filterOption").dataset.id))} />
                        </div>
                    </div>
                ))}
            </FilterType>
            <ButtonAdd text="Add Label" onAdd={addLabel}/>
        </div>
    )
}

export default SideBar